<style>
.shortcuts-menu li {
    width: 100%;
    max-width: 33%;
    padding: 15px;
    float: left;
    display: flex;
    transition: all .4s;
}
</style>

<template>
    <section>
        <div class="modal my-popups right fade show" data-keyboard="false" data-backdrop="static" id="manageChannelModal"
            role="dialog" style="display:block!important">
            <div class="modal-dialog mw-1100" role="document">
                <div class="modal-content">
                    <div class="modal-header mheader-user">
                        <button type="button" class="close" @click="close"><i class="fa fa-times"
                                aria-hidden="true"></i></button>
                        <h4 class="modal-title" id="myModalLabel">{{ title }}</h4>
                    </div>
                    <div class="modal-body" style="background:white;">
                        <ul class="shortcuts-menu pb-3" id="divChannelTypes" v-if="tab === 'main'">
                            <template v-for="(option, index) in channelOptions">
                                <li class="position-relative" @click="ExpandMenu(option, index)">
                                    <a class="short-menu-item"
                                        v-bind:style="{ 'background': shadeColor(option.CHANNEL_COLOR, 70) }">
                                        <span class="left-icon-m m-3 text-white"
                                            v-bind:style="{ 'background': option.CHANNEL_COLOR }">
                                            <i v-bind:class="option.CHANNEL_ICON"></i>
                                        </span>
                                        <span class="short-item-name">{{ option.CHANNEL_DESC }}</span>
                                    </a>
                                </li>

                                <template v-if="((index + 1) % 3 == 0 && Math.ceil((index + 1) / 3) == subMenuRow)">
                                    <AddSubMenuPopup v-if="AddSubMenuPopup" v-bind:class="subMenuClass" size="s"
                                        v-bind:showBorder="true" v-bind:channelOptions="subMenuOptions"></AddSubMenuPopup>
                                </template>
                            </template>
                        </ul>

                        <template v-if="tab == 'Email'">
                            <AddSubMenuPopup v-if="AddSubMenuPopup" v-bind:showBorder="false" size="m"
                                v-bind:channelOptions="customEmailOptions"></AddSubMenuPopup>
                        </template>

                        <dynamic-form ref="imapSettings" id="imapSettingTab" :buttons="false" lang="en"
                            v-if="tab == 'imapSettings'" :schema="FormSchema" v-on:OnSubmit="setEmailType('Custom')">
                            <template v-slot:tgslot-Password="{ data }">
                                <div class="input-group">
                                    <input type="password" class="form-control w-100" v-model="data.fieldInfo.value"
                                        :name="'Password'">
                                </div>
                            </template>
                        </dynamic-form>

                        <dynamic-form ref="exchangeSettings" id="exchangeSettingsTab" :buttons="false" lang="en"
                            v-if="tab == 'exchangeSettings'" :schema="FormSchemaExchange"
                            v-on:OnSubmit="setEmailType('Custom')">
                            <template v-slot:tgslot-Password="{ data }">
                                <div class="input-group">
                                    <input type="password" class="form-control w-100" v-model="data.fieldInfo.value"
                                        :name="'Password'">
                                </div>
                            </template>
                        </dynamic-form>



                        <div class="modal-footer fixed-bottom">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                    <span class="text-danger float-left"
                                        v-if="tab == 'imapSettings' || tab == 'exchangeSettings'"><small><span
                                                class="text-danger">Fields marked with an asterisk (*) are
                                                mandatory.</span></small></span>
                                </div>
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-right">
                                    <div class="input-group text-right d-block">
                                        <button type="button" class="btn btn-secondary mr-1" v-if="tab == 'Email'"
                                            @click="openTab('main')"><i class="fa fa-angle-double-left mr-1"></i>{{
                                                $t('Back') }}</button>
                                        <button type="button" class="btn btn-danger mr-1" v-if="tab == 'EmailSetUpSuccess'"
                                            @click="close"><i class="fa fa-close mr-1"></i>{{ $t('Close') }}</button>
                                        <button type="button" class="btn btn-success mr-1"
                                            v-if="tab == 'imapSettings' || tab == 'exchangeSettings'"
                                            @click="setEmailType('Custom')"><i class="fa fa-save mr-1"></i>{{
                                                $t('SaveSettings') }}</button>
                                        <button type="button" class="btn btn-secondary mr-1"
                                            v-if="tab == 'imapSettings' || tab == 'exchangeSettings'"
                                            @click="openTab('Email')"><i class="fa fa-angle-double-left mr-1"></i>{{
                                                $t('Back') }}</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>



    </section>
</template>

<script>

import DataServices from "@/services/DataService";
import AddSubMenuPopup from '../../components/Modal/AddSubMenuPopup.vue'


export default {
    components: { AddSubMenuPopup },
    data() {
        return {
            errors: {},
            SourceId: 0,
            AddSubMenuPopup: false,
            AddChannel: null,
            tabchange: null,
            title: 'Setup Ticket Channel',
            menuItemSize: 25,
            isLoading: false,
            TicketId: null,
            tab: 'main',
            showSubMenu: false,
            subMenuClass: 'sub-menu-section-center',
            subMenuRow: 1,
            channelOptions: [],
            selectedChannel: null,
            subMenu: '',
            subMenuOptions: [],
            callAndMessageOptions: [],
            whatsappOptions: [],
            chatbotOption: [],
            externalLinkOptions: [],
            TimeZoneList: [],
            isVisible: false,
            FormSchema: [
                {
                    layoutType: "double",
                    group_name: 'User Information',
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Emails Account Type'),
                            name: "EmailsAccountType",
                            value: "IMAP",
                            placeholder: "",
                            disabled: true,
                            config: {
                                options: [{ value: "IMAP", name: "IMAP" }],
                                showAddIcon: false,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Display Name'),
                            name: "DisplayName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Email Address'),
                            name: "EmailAddress",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('Password'),
                            name: "Password",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    group_name: 'Incoming Server Setting',
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Incoming Mail Server'),
                            name: "IncomingMailServer",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Is Secure Connection'),
                            name: "IsIncomingSecureConnection",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: true }
                                ]
                            },
                            validationRules: { "required": false },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Port'),
                            name: "Port",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Port Incoming Checked'),
                            name: "PortIncomingChecked",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: true }
                                ]
                            },
                            validationRules: { "required": false },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    group_name: 'Outgoing Server Setting',
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Outgoing Server (SMTP)'),
                            name: "OutgoingServer",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Is Secure Connection'),
                            name: "IsOutgoingSecureConnection",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: true }
                                ]
                            },
                            validationRules: { "required": false },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Port'),
                            name: "Outgoing Port",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Port Outgoing Checked'),
                            name: "PortOutgoingChecked",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: true }
                                ]
                            },
                            validationRules:  { "required": false }
                        },
                    ],
                }
            ],
            FormSchemaExchange: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Exchange Server Version'),
                            name: "ExchangeServerVersion",
                            value: "2013",
                            placeholder: "",
                            config: {
                                options: [{ value: "2013", name: "2013", selected: "selected" }],
                                showAddIcon: false,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Exchange Domain Name'),
                            name: "ExchangeDomainName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('User Name'),
                            name: "UserName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('Password'),
                            name: "Password",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Time Zone'),
                            name: "TimeZone",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: { "required": true },
                        },
                    ]
                },
            ],
            emailOptions: [
                {
                    name: 'setupTalygenEmail',
                    title: 'Talygen Email',
                    icon: 'fa fa-envelope',
                    color: '#e5a30ebf',
                    background: 60
                },
                {
                    name: 'setupCustomEmail',
                    title: 'Add Custom Email',
                    icon: 'fa fa-envelope',
                    color: '#008de0c9',
                    background: 60

                }
            ],
            customEmailOptions: [
                {
                    name: 'imap',
                    title: 'IMAP',
                    icon: 'fa fa-envelope',
                    color: '#00000082',
                    background: 60
                },
                {
                    name: 'exchange',
                    title: 'Exchange',
                    icon: 'fa fa-envelope',
                    color: '#00000082',
                    background: 60
                }
            ],
            socialMediaOptions: [{
                name: 'facebook',
                title: 'Connect with Facebook',
                icon: 'fa fa-facebook-square',
                color: '#002d7ef2',
                background: 0
            },
            {
                name: 'twitter',
                title: 'Connect with Twitter',
                icon: 'fa fa-twitter-square',
                color: '#008de0c9',
                background: 0
            },
            {
                name: 'instagram',
                title: 'Connect with Instagram',
                icon: 'fa fa-instagram',
                color: '#cf0949f0',
                background: 0
            },
            {
                name: 'linkedin',
                title: 'Connect with Linkedin',
                icon: 'fa fa-linkedin-square',
                color: '#006bc7f0',
                background: 0
            }],
            imapSettings: {
                sourceId: 0,
                emailAccountType: 'IMAP',
                displayName: '',
                email: '',
                password: '',
                incomingMailServer: '',
                isSecureConnection: false,
                port: '',
                portIncomingChecked: false,
                outgoingServer: '',
                isOutgoingSecureConnection: false,
                outPort: '',
                portOutgoingChecked: false
            },
            exchangeSettings: {
                sourceId: 0,
                exchangeServerVersion: '2013',
                exchangeDomainName: '',
                userName: '',
                password: '',
                timeZone: ''
            },

        }
    },
    props: {
        tabChange: {
            type: String,
            required: false
        },
        SourceId: {
            type: Number,
            required: false
        },
        AddChannel: {
            type: String,
            required: false
        },
        List: {
            type: Array,
            required: false
        }
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.tabchange = this.tabChange;
        vm.AddChannel = this.AddChannel;
        vm.SourceId = this.SourceId;
        vm.selectedChannel = {
            channelName: this.List.CHANNEL_NAME,
            channelId: this.List.CHANNEL_ID
        };
        if (vm.AddChannel == 'Addticketing') {
            vm.getChannelOptions();
            this.AddChannel = null;
        }
        if (vm.tabchange != null && vm.AddChannel == 'Edit') {
            if (vm.tabchange == 'imapSettings') {
                vm.openTab('imapSettings');
            }
            else {
                vm.openTab('exchangeSettings');
            }
            this.setMappingList(this.List);
            this.tabchange = null;
            this.AddChannel = null;
        }
    },
    methods: {
        GetTimeZoneList: function () {
            debugger;
            var vm = this;
            DataServices.GetTimeZoneListing().then(function (response) {
                debugger;
                if (response.data.result.length > 0) {
                    vm.TimeZoneList = response.data.result;
                    debugger
                    vm.TimeZoneList.forEach((item, index) => {
                        (vm.FormSchemaExchange)[2].Data[0].config.options.push({
                            value: item.VALUE,
                            name: item.TEXT
                        })
                    })
                }
            });
        },
        setMappingList: function (data) {
            debugger;
            var vm = this;
            if (data.TYPE == 'IMAP') {
                vm.$parent.SOURCE_ID,
                    this.FormSchema[0].Data[1].value = data.USERNAME,
                    this.FormSchema[1].Data[0].value = data.EMAIL,
                    this.FormSchema[1].Data[1].value = data.PASSWORD,
                    this.FormSchema[2].Data[0].value = data.IP,
                    this.FormSchema[3].Data[0].value = data.PORT,
                    this.FormSchema[2].Data[1].value = data.IS_INCOMING_PORT_CHECKED,
                    this.FormSchema[3].Data[1].value = data.IS_INCOMING_CONNECTION_SECURE,
                    this.FormSchema[4].Data[0].value = data.OUTGOING_IP,
                    this.FormSchema[5].Data[0].value = data.OUTGOING_PORT,
                    this.FormSchema[4].Data[1].value = data.IS_OUTGOING_PORT_CHECKED,
                    this.FormSchema[5].Data[1].value = data.IS_OUTGOING_CONNECTION_SECURE
            }
            else {
                vm.$parent.SourceId,
                    this.FormSchemaExchange[0].Data[0].value = data.EXCHANGE_SERVER_VERSION,
                    this.FormSchemaExchange[0].Data[1].value = data.DOMAIN,
                    this.FormSchemaExchange[1].Data[0].value = data.USERNAME,
                    this.FormSchemaExchange[1].Data[1].value = data.PASSWORD,
                    this.FormSchemaExchange[2].Data[0].value = data.TIMEZONE_ID
            }
        },
        LoginWithTwitter: function () {
            debugger;
            DataServices.GetTwitterAuth().then(function (response) {
                debugger;
                window.location = response.data;
            });
        },
        shadeColor: function (color, percent) {
            debugger // add channel config
            color = color.length == 6 ? color : color.slice(0, -2);
            var newColor = color + percent.toString(16).toUpperCase().padStart(2, '0');
            return newColor;
        },
        openTab: function (tab) {
            debugger;
            var vm = this;
            if (tab === 'Email') {
                vm.title = 'Email Setup';
            }
            else if (tab === 'main') {
                vm.title = 'Setup Ticket Channel';
                vm.selectedChannel = null;
            }
            else if (tab === 'imapSettings') {
                vm.title = 'IMAP Settings';
            }
            else if (tab === 'exchangeSettings') {
                vm.title = 'Exchange Settings';
                vm.GetTimeZoneList();
            }
            vm.tab = tab;
            vm.AddSubMenuPopup = true;
        },

        ExpandMenu: function (tab, index) {
            debugger;
            var vm = this;
            vm.showSubMenu = false;
            var tabNum = Math.ceil((index + 1) / 3);
            vm.subMenuRow = tabNum;
            vm.subMenuOptions = [];
            vm.selectedChannel = {
                channelName: tab.CHANNEL_NAME,
                channelId: tab.CHANNEL_ID
            };

            if (tab.CHANNEL_NAME === 'Email') {
                vm.subMenuOptions = vm.emailOptions;
            }
            else if (tab.CHANNEL_NAME === 'SocialMedia') {
                vm.subMenuOptions = vm.socialMediaOptions;
            }
            else if (tab.CHANNEL_NAME === 'ExternalLink') {
                vm.subMenuOptions = vm.externalLinkOptions;
            }
            else if (tab.CHANNEL_NAME === 'Chatbot') {
                vm.subMenuOptions = vm.chatbotOption;
            }
            else if (tab.CHANNEL_NAME === 'WhatsApp') {
                vm.subMenuOptions = vm.whatsappOptions;
            }
            else if (tab.CHANNEL_NAME === 'CallMessage') {
                vm.subMenuOptions = vm.callAndMessageOptions;
            }

            if (vm.subMenu == '' || vm.subMenu != tab.CHANNEL_NAME) {
                vm.subMenuClass = 'sub-menu-section-right';
                if ((index + 1) % 3 === 2) {
                    vm.subMenuClass = 'sub-menu-section-center';
                }
                else if ((index + 1) % 3 === 1) {
                    vm.subMenuClass = 'sub-menu-section-left';
                }
                if (vm.subMenuOptions.length > 0) {
                    vm.AddSubMenuPopup = true;
                }
                vm.subMenu = tab.CHANNEL_NAME;
            }
            else {
                vm.AddSubMenuPopup = false;
                vm.subMenu = '';
                vm.selectedChannel = null;
            }
        },
        getChannelOptions: function () {
            //debugger
            var vm = this;
            DataServices.FetchTicketingChannelOptionList().then(function (response) {
                debugger;
                if (response.data.result.data != null) {
                    if (response.data.result.data.length > 0) {
                        vm.channelOptions = response.data.result.data;
                    }
                }
            });
        },
        setEmailType: function (source) {
            debugger;
            var msg = "";
            var vm = this;
            if (source === 'Talygen') {
                vm.confirmR(vm.$t("Are you sure you want to set Talygen email as ticket channel?"), true, vm.$t("Delete"), function () {
                    let data = {
                        CHANNEL_NAME: vm.selectedChannel.channelName,
                        CHANNEL_SOURCE_TYPE_NAME: source
                    };
                    var jsonData = JSON.stringify(data);

                    DataServices.SubmitTicketingChannelConfiguration(jsonData).then(function (response) {

                        if (response.data.result === 'Success') {
                            vm.tab = 'EmailSetUpSuccess';
                            vm.$parent.getChannelOptions();
                            vm.ShowAlert(vm.$t('EmpShareSentSuccessfully'), "success", true, vm.$t('Alert'));
                        }
                        else if (response.data.result === 'ALREADY EXISTS') {
                            vm.ShowAlert(vm.$t('RequestAlreadySent'), "success", true, vm.$t('Alert'));
                        }
                    });

                },
                    function (response) {

                    });
            }
            else {
                debugger;
                var sourceData = {};
                var isDataValidated = false;
                if (source === 'Custom') {
                    if (vm.tab === 'imapSettings') {
                        sourceData = {
                            SOURCE_ID: vm.SourceId,
                            USERNAME: this.FormSchema[0].Data[1].value,
                            EMAIL: this.FormSchema[1].Data[0].value,
                            PASSWORD: this.FormSchema[1].Data[1].value,
                            IP: this.FormSchema[2].Data[0].value,
                            PORT: this.FormSchema[3].Data[0].value,
                            IS_INCOMING_PORT_CHECKED: this.FormSchema[2].Data[1].value == null ? null : this.FormSchema[2].Data[1].value[1],
                            IS_INCOMING_CONNECTION_SECURE: this.FormSchema[3].Data[1].value == null ? null : this.FormSchema[3].Data[1].value[1],
                            OUTGOING_IP: this.FormSchema[4].Data[0].value,
                            OUTGOING_PORT: this.FormSchema[5].Data[0].value,
                            IS_OUTGOING_PORT_CHECKED: this.FormSchema[4].Data[1].value == null ? null : this.FormSchema[4].Data[1].value[1],
                            IS_OUTGOING_CONNECTION_SECURE: this.FormSchema[5].Data[1].value == null ? null : this.FormSchema[5].Data[1].value[1],
                            TYPE: 'IMAP'
                        };
                    }
                    else {
                        sourceData = {
                            SOURCE_ID: vm.SourceId,
                            EXCHANGE_SERVER_VERSION: this.FormSchemaExchange[0].Data[0].value,
                            DOMAIN: this.FormSchemaExchange[0].Data[1].value,
                            USERNAME: this.FormSchemaExchange[1].Data[0].value,
                            PASSWORD: this.FormSchemaExchange[1].Data[1].value,
                            TIMEZONE_ID: this.FormSchemaExchange[2].Data[0].value,
                            TYPE: 'EXCHANGE'
                        };
                    }
                    var form = vm.tab === 'imapSettings' ? this.$refs.imapSettings : this.$refs.exchangeSettings;

                    form.validateForm.validate().then(function (valid) {
                        debugger;
                        let data = {
                            CHANNEL_NAME: vm.selectedChannel.channelName,
                            CHANNEL_SOURCE_TYPE_NAME: source,
                            CHANNEL_SOURCE_DATA: sourceData
                        };


                        var jsonData = JSON.stringify(data);
                        if (valid) {
                            DataServices.SubmitTicketingChannelConfiguration(jsonData).then(function (response) {
                                if (response.data.status === 'Success') {
                                    vm.ShowAlert(vm.$t("SavedSuccessfully", [vm.$t(vm.tab)]), "success", true, vm.$t("Alert"));
                                    vm.close();
                                    vm.getChannelOptions();
                                    vm.$parent.FetchData();
                                }
                                if (response.data == "INVALID_CREDENTIAL") {
                                    vm.ShowAlert(vm.$t("PleaseCheckTheCredentials", [vm.$t(vm.tab)]), "failure", true, vm.$t("Alert"));
                                }
                                if (response.data == "CHECKBOX_UNCHECKED") {
                                    vm.ShowAlert(vm.$t("CheckboxPending", [vm.$t(vm.tab)]), "failure", true, vm.$t("Alert"));
                                }
                            });
                        }
                        else {
                            vm.ShowAlert(vm.$t("FillTheRequiredFields", [vm.$t(vm.tab)]), "failure", true, vm.$t("Alert"));
                        }
                    });
                }
            }
        },
        close: function () {
            this.$parent.AddChannelConfigurationPopup = false;
        },

    }
}
</script>