<style scoped>
 .multi-collapse {
        padding-left: 15px !important;
        padding-right: 15px !important;
        padding-top: 0px !important;
        padding-bottom: 0px !important;
        max-width: 99% !important;
    }

    .sub-menu li {
        width: 100%;
        max-width: 50%;
        padding: 15px;
        float: left;
        display: flex;
        transition: all .4s;
    }

        .sub-menu li .short-item-name {
            font-size: 15px;
            color: black !important;
        }

    .shortcuts-menu li .short-item-name {
        color: black !important;
    }

    .sub-menu-section-right:before {
        content: '';
        position: absolute;
        left: 66%;
        right: 0;
        top: -8px;
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent transparent rgba(0,0,0,.2) transparent;
        border-width: 0px 10px 10px 10px;
    }

    .sub-menu-section-left:before {
        content: '';
        position: absolute;
        left: 0;
        right: 66%;
        top: -8px;
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent transparent rgba(0,0,0,.2) transparent;
        border-width: 0px 10px 10px 10px;
    }

    .sub-menu-section-center:before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: -8px;
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent transparent rgba(0,0,0,.2) transparent;
        border-width: 0px 10px 10px 10px;
    }

    .shortcuts-menu li .left-icon-m i {
        color: white !important;
        font-size: 25px !important;
    }

    .shortcuts-menu li:hover {
        transform: inherit !important;
    }

    .shortcuts-menu li {
        width: 100%;
        max-width: 33%;
        padding: 15px;
        float: left;
        display: flex;
        transition: all .4s;
    }

        .shortcuts-menu li.large {
            width: 100%;
            max-width: 50%;
            padding: 15px;
            float: left;
            display: flex;
            transition: all .4s;
        }

        .shortcuts-menu li .short-item-name {
        color: black !important;
    }
</style>


<template>  
        <div class="collapse multi-collapse show position-relative" id="multiCollapse">
        <div v-bind:class="{'card':showBorder}">
            <ul v-bind:class="{'sub-menu':size ==='s','shortcuts-menu':size ==='m'}">
                <template v-for="menuItem in channelOptions">                   
                    <li class="position-relative large" v-bind:style="{'width':menuItemSize+'%!important'}" @click="goToAction(menuItem.name)">
                        <a class="short-menu-item" v-bind:style="{'background':shadeColor(menuItem.color,menuItem.background),'border-color':menuItem.color}" v-if="size==='s'">
                            <span class="icon m-2 text-white">
                                <i v-bind:class="menuItem.icon" v-bind:style="{'color':menuItem.color}"></i>
                            </span>
                            <span class="short-item-name">{{menuItem.title}}</span>
                        </a>
                        <a class="short-menu-item" v-bind:style="{'background':shadeColor(menuItem.color,menuItem.background),'border-color':menuItem.color}" v-else-if="size==='m'">
                            <span class="left-icon-m m-3 text-white" v-bind:style="{'background':menuItem.color}">
                                <i v-bind:class="menuItem.icon"></i>
                            </span>
                            <span class="short-item-name">{{menuItem.title}}</span>
                        </a>
                    </li>
                </template>
            </ul>
        </div>
    </div>  
</template>

<script>
import DataServices from "@/services/DataService";


export default {
    props: {            
            showBorder: {
                type: Boolean,
                required: false
            },
            size: {
                type: String,
                required: true
            },
            channelOptions: {
                type:Array,
                required: true
            }
        },
    data() {
        return {
            FacebookAppId: "1977869235924234",
            FacebookConfigurationId: "854925422657929",
            FacebookAuthCallBackUrl: "https://stage.talygen.com/CallBack/FacebookAuth",
            menuItemSize:25,
            subMenuClass: 'sub-menu-section-center',
            isLoading: false,
            TicketId: null, 
            tab: 'main',
            showSubMenu: false,            
            subMenuRow: 1,
            selectedChannel: null,
            subMenu: '',
            subMenuOptions: [],          
            isVisible:false,
            emailOptions: [
                    {
                        name: 'setupTalygenEmail',
                        title: 'Talygen Email',
                        icon: 'fa fa-envelope',
                        color: '#e5a30ebf',
                        background:60
                    },
                    {
                        name: 'setupCustomEmail',
                        title: 'Add Custom Email',
                        icon: 'fa fa-envelope',
                        color: '#008de0c9',
                        background: 60

                    }
                ],  
                imapSettings: {
                    sourceId:0,
                    emailAccountType: 'IMAP',
                    displayName: '',
                    email: '',
                    password: '',
                    incomingMailServer: '',
                    isSecureConnection: false,
                    port: '',
                    portIncomingChecked: false,
                    outgoingServer: '',
                    isOutgoingSecureConnection: false,
                    outPort: '',
                    portOutgoingChecked: false
                },
                exchangeSettings: {
                    sourceId: 0,
                    exchangeServerVersion: '2013',
                    exchangeDomainName: '',
                    userName: '',
                    password: '',
                    timeZone: ''
                },
                  
        }
    },   
    watch:{
            channelOptions:function(value){
                var vm = this;
                vm.setMenuItemSize();
            }
        },
    created: async function () {
        debugger;
        var vm = this;            
        vm.setMenuItemSize();
        
    },
       methods: {       
        setMenuItemSize: function(){
            debugger;
                var vm=this;
                var length = vm.channelOptions.length;
                if(length>4)
                {
                    vm.menuItemSize=25;
                }
                else{
                        vm.menuItemSize = Math.ceil(100 / length);
                        console.log(vm.menuItemSize);
                }
            }, 
        shadeColor: function(color, percent)
        {
            debugger; //add sub menu
            color = color.length == 6 ? color : color.slice(0, -2);
          var newColor = color + percent.toString(16).toUpperCase().padStart(2, '0');
           return newColor;
        },
        goToAction: function (action) {
            debugger;
                var vm = this;
                if (action == 'Email') {
                    vm.tab = "Email";
                }
                if (action == 'setupTalygenEmail') {
                    vm.$parent.setEmailType('Talygen');
                }
                else if (action == 'twitter') {
                    vm.$parent.LoginWithTwitter();
                }
                 else if (action == 'facebook') {
                    debugger;
                    window.location.href = "https://graph.facebook.com/oauth/authorize?client_id="+ vm.FacebookAppId.toString() +"&config_id="+ vm.FacebookConfigurationId.toString() +"&redirect_uri="+ vm.FacebookAuthCallBackUrl.toString() +"";                    
                }
                else if (action == 'setupCustomEmail') {
                    vm.$parent.openTab('Email');
                }
                else if (action == 'imap') {
                    vm.$parent.openTab('imapSettings');
                }
                else if (action == 'exchange') {
                    vm.$parent.openTab('exchangeSettings');
                }
            }, 
            close: function () {
            this.$parent.AddSubMenuPopup = false;
        },
       
    }
}
</script>
