<template>
    <section>
        <tg-list v-show="IsGroupView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FSM_Ticketing_Setup_Source"
            IdentityColumn="SOURCE_ID" :HeaderText="$t('Channel Configuration Lis....')" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" :widgets="[]" :listType="['List']" :ListData="channelConfigList"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">               
                <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                    <listing-status :isfor="'ChannelConfiguration'"
                        v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')" ref="updatestatus"
                        v-bind:dataid="data.row.SOURCE_ID" data-controller="Crm" v-bind:statuslist="StatusListForListing"
                        v-bind:item="data.STATUS_ID" data-action="index"
                        v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')">
                        <span v-bind:class="getStatusClass(data.row.STATUS_ID, 'STATUS_ID')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
            </template>
        </tg-list>
        <AddChannelConfigurationPopup v-if="AddChannelConfigurationPopup" ref="ChannelConfiguration" :SourceId="SourceId" :AddChannel="AddChannel" :List="SettingList" :tabChange="tabChange"></AddChannelConfigurationPopup>
    </section>    
</template>

<script>
import Schema from "@/views/ChannelConfiguration/ChannelConfigurationSchema";
import DataServices from "@/services/DataService";
import AddChannelConfigurationPopup from '../../components/Modal/AddChannelConfigurationPopup.vue'

export default {
    components:{AddChannelConfigurationPopup},
    data() {
        return {
            tabChange:null,
            channelConfigList: [],
            SettingList:[],
            AddChannelConfigurationPopup:false,
            showChannelModal: false,    
            mapStatusId: 0,
            SourceId:0,
            AddChannel:null,
            TotalRecords: 0,
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            SortBy: '',
            SortExp: '',
            user_type: null,
            user_id: null,
            companyId: null,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": "ACCOUNT_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Account Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "CHANNEL_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Channel Name",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "CREATED_BY",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created By",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 4
            }    
            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            IsGroupView: true,
            noRecord: false,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
        }
    },
    created: async function () {        
        var vm = this;
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await this.FetchData();
        await Schema.bindleftCommonSearchdropdown(vm, "FSM", "FSM_USERS");
    },
    methods: {
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        },
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {            
            switch (callbackfunction) {
                case 'commonDeleteData':
                    this.commonDelete(item.SOURCE_ID);
                    break;
                case 'editChannelConfig':
                    this.editChannelConfig(item.SOURCE_ID, item.CHANNEL_NAME);
                    break;
            }
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "openaddAddchannel":
                    this.openAddchannelConfiguration(event.id);
                    break;
            }
        },
        openAddchannelConfiguration: function (id) {
                if(id == undefined)
                    id = 0;                
                this.AddChannel = id;
                this.AddChannelConfigurationPopup = !this.AddChannelConfigurationPopup;
            },
            editChannelConfig: function (SOURCE_ID, CHANNEL_NAME) {            
                var vm = this;               
                if(CHANNEL_NAME==='Email'){                  
                    var params=`postString=${SOURCE_ID}`
                    DataServices.GetTicketingChannelSourceById(params).then(response => {
                        if (response.data.result.data != null) {
                            if (response.data.result.data.length > 0) {                                
                                var data = response.data.result.data[0];                                
                                if (data.TYPE.toUpperCase() === 'IMAP') {
                                    vm.tabChange='imapSettings';
                                    vm.AddChannel = 'Edit';
                                    vm.SettingList = data;
                                    vm.SourceId = SOURCE_ID;
                                    vm.AddChannelConfigurationPopup=true;                                                                                                
                                }
                                else if (data.TYPE.toUpperCase() === 'EXCHANGE') {
                                    vm.tabChange='exchangeSettings';
                                    vm.AddChannel = 'Edit';
                                    vm.SettingList = data;
                                    vm.AddChannelConfigurationPopup=true;                                    
                                }
                            }
                        }
                    });
                }
            },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        FetchData: async function () {            
            var vueObj = this;
            var srchCondition = "";
            const NameRegex = /LIKE '%(.*?)%'/;
            const valuesInParenthesesRegex = /'([^']+)'/;
            var url = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}`;

            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }

            if (!this.isBlank(srchCondition)) {
                if (srchCondition.includes("account_name")) {
                    var accountNameMatch = srchCondition.match(NameRegex);
                    url = url + "&accountName=" + accountNameMatch[1];
                }
                else if (srchCondition.includes("channel_name")) {
                    var channelNameMatch = srchCondition.match(NameRegex);
                    url = url + "&channelName=" + channelNameMatch[1];
                }
                else if (srchCondition.includes("created_by")) {
                    var created_by_match = srchCondition.match(valuesInParenthesesRegex);
                    url = url + "&createdBy=" + created_by_match[1];
                }
                else if (srchCondition.includes("Status")) {
                    var status_Match = srchCondition.match(valuesInParenthesesRegex);
                    url = url + "&statusId=" + status_Match[1];
                }
            }
            await DataServices.FetchFSMTicketingSetupSources(url).then(function (response) {                
                if (response.data != "" && response.data != null) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "STATUS_ID") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == 'Action') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                    if (response.data.result.DATA.length > 0) {                        
                        vueObj.channelConfigList = response.data.result.DATA;   
                        vueObj.TotalRecords = response.data.result.DATA.length;
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.listheaderbuttons[1].isdisabled=true;
                        vueObj.noRecord = false;
                    }
                    else {
                        vueObj.channelConfigList = response.data.result.DATA; 
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }                
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },       
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        commonDeleteData: function (event) {            
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Resource")), function () {                  
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "DELETE_FSM_TICKET_SOURCE"

                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {                       
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            vueObj.listheaderbuttons[1].isdisabled=true;
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        commonDelete: function (item) {           
            var vm = this;
            if (item > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Resource")), function () {                   
                    var postJSON = {
                        ids: item,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "DELETE_FSM_TICKET_SOURCE"

                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {                        
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteItem']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        UpdateStatus: async function (status) {  
            var vm = this;
            if (status == 'Active') {
                status = 1001
            }
            else {
                status = 1002
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("Confirm"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var url = `id=${val}&status_id=${status}&isFor=${'group'}`;
                    DataServices.updatestatus(url).then(response => {                        
                        if (response.data == true) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchData();

                        }

                    })


                })
            }
        }
    }


}
</script>